import { DateService } from "./../shared/service/date.service";
import {
	AfterViewInit,
	Component,
	OnDestroy,
	OnInit,
	ViewEncapsulation
} from "@angular/core";
import * as _ from "lodash";
import { Subscription, forkJoin } from "rxjs";
// import { CodeNumValue } from "../project/models/codenumvalue.model";
// import { PoP } from "../project/models/pop.model";
import { DashboardSearchCriteria } from "./models/dash-search-criteria.model";
import { TDLDashboardInfo } from "./models/dash-tdl-info.model";
import { SpendCeilingInfo } from "./models/spendceiling.model";
import { DashboardService } from "./services/dashboard.service";
import { ProjectShortSummary } from "./models/proj-shortsum.model";
import { CodeNumValue } from '../tdl/models/codenumvalue.model';
import { PoP } from '../tdl/models/pop.model';

@Component({
	selector: "[my-dashboard]",
	templateUrl: "./mydashboard.template.html",
	encapsulation: ViewEncapsulation.None,
	styleUrls: ["./mydashboard.style.scss"],
	providers: []
})
export class MyDashboardPage implements OnInit, OnDestroy, AfterViewInit {
	public busytdlDBInfo: Subscription;
	public busyLoadPops: Subscription;
	public busyLoadSpendCeilingInfo: Subscription;
	public busyLoadProjectSummary: Subscription;

	public tdlDBInfo: TDLDashboardInfo;
	public tdlStatusBreakdownInfo: CodeNumValue[];
	public tdlTypeBreakdownInfo: CodeNumValue[];
	public projSpendInfo: SpendCeilingInfo[];
	public actSpendInfo: SpendCeilingInfo[];

	public listPoPs: PoP[];
	public listProjects: ProjectShortSummary[];

	public selpopCode: string = "T03";

	public pieTypeChartLabels: string[] = [];
	public pieTypeChartData: number[] = [];

	public pieStatusChartLabels: string[] = [];
	public pieStatusChartData: number[] = [];

	public pieChartType: string = "pie";
	public pieChartOptions: any = {
		scaleShowVerticalLines: false,
		responsive: true,
		legend: {
			display: false
		}
	};
	public pieChartColors: Array<any> = [
		{
			backgroundColor: [
				"#5DA5DA",
				"#FAA43A",
				"#60BD68",
				"#F17CB0",
				"#B2912F",
				"#B276B2",
				"#DECF3F",
				"#4D4D4D",
				"#F15854"
			],
			borderColor: [
				"rgba(135,206,250,1)",
				"rgba(106,90,205,1)",
				"rgba(148,159,177,1)"
			]
		}
	];

	// @ViewChild(BaseChartDirective) _chart: BaseChartDirective;

	private showErrorFlag: boolean;

	constructor(private dashboardService: DashboardService) {
		this.showErrorFlag = false;
		this.tdlDBInfo = new TDLDashboardInfo();
	}

	ngOnInit(): void {
		let start: Date = DateService.getDateBySubtractingDaysFromToday(29);
		let end: Date = DateService.getToday();

		this.refreshDashboardForDateRange(
			DateService.getFormattedText(start, "YYYY-MM-DD"),
			DateService.getFormattedText(end, "YYYY-MM-DD")
		);
		this.loadProjectShortSummary();
	}

	ngAfterViewInit(): void {}

	ngOnDestroy(): void {
		if (this.busyLoadPops) {
			this.busyLoadPops.unsubscribe();
		}
		if (this.busytdlDBInfo) {
			this.busytdlDBInfo.unsubscribe();
		}
		if (this.busyLoadSpendCeilingInfo) {
			this.busyLoadSpendCeilingInfo.unsubscribe();
		}
		if (this.busyLoadProjectSummary) {
			this.busyLoadProjectSummary.unsubscribe();
		}
	}

	public loadProjectShortSummary() {
		this.busyLoadProjectSummary = this.dashboardService
			.getTopUpcomingProjectsDue()
			.subscribe(
				data => {
					if (data) {
						this.listProjects = data;
					}
				},
				error => {
					// this.showErrorFlag = true;
					// this.alerts = [];
					// this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	public loadTDLInfo(dsc: DashboardSearchCriteria) {
		let handler = this;
		// Total Active TDL Numbers, Cost and Hours
		this.busytdlDBInfo = this.dashboardService
			.getTDLDashboardInfo(dsc)
			.subscribe(
				data => {
					if (data) {
						handler.tdlDBInfo = data;
					}
				},
				error => {
					this.showErrorFlag = true;
					// this.alerts = [];
					// this.alerts.push({type: 'warning', msg: error});
				}
			);
	}

	public loadTDLTypeInfo(dsc: DashboardSearchCriteria) {
		let handler = this;
		// Total Active TDL Numbers, Cost and Hours
		this.busytdlDBInfo = this.dashboardService
			.getTDLDashboardTypeInfo(dsc)
			.subscribe(
				data => {
					if (data) {
						handler.tdlTypeBreakdownInfo = data;
						let typebdlabels: string[] = _.map(
							handler.tdlTypeBreakdownInfo,
							"code"
						);
						handler.pieTypeChartData = _.map(
							handler.tdlTypeBreakdownInfo,
							"numvalue"
						);

						handler.pieTypeChartLabels.length = 0;
						for (let i = 0; i < typebdlabels.length; i++) {
							handler.pieTypeChartLabels.push(typebdlabels[i]);
						}
					}
				},
				error => {
					this.showErrorFlag = true;
					// this.alerts = [];
					// this.alerts.push({type: 'warning', msg: error});
				}
			);
	}

	public loadTDLStatusInfo(dsc: DashboardSearchCriteria) {
		let handler = this;
		// Total Active TDL Numbers, Cost and Hours
		this.busytdlDBInfo = this.dashboardService
			.getTDLDashboardStatusInfo(dsc)
			.subscribe(
				data => {
					if (data) {
						handler.tdlStatusBreakdownInfo = data;
						let statusbdlabels: string[] = _.map(
							handler.tdlStatusBreakdownInfo,
							"code"
						);
						handler.pieStatusChartData = _.map(
							handler.tdlStatusBreakdownInfo,
							"numvalue"
						);

						this.pieStatusChartLabels.length = 0;
						for (let i = 0; i < statusbdlabels.length; i++) {
							this.pieStatusChartLabels.push(statusbdlabels[i]);
						}
					}
				},
				error => {
					this.showErrorFlag = true;
					// this.alerts = [];
					// this.alerts.push({type: 'warning', msg: error});
				}
			);
	}

	public getProjectedSpendValue(code: string): number {
		let retValue = 0.0;
		let si: SpendCeilingInfo = new SpendCeilingInfo();
		let rowIndex = _.findIndex(this.projSpendInfo, function(o) {
			return o.category === code;
		});
		if (rowIndex !== -1) {
			retValue = this.projSpendInfo[rowIndex].cost;
		}
		return retValue;
	}

	public getProjectedSpendPercent(code: string): number {
		let retValue = 0.0;
		let rowIndex = _.findIndex(this.projSpendInfo, function(o) {
			return o.category === code;
		});
		if (rowIndex !== -1) {
			retValue =
				this.projSpendInfo[rowIndex].spendLimit === 0.0
					? 0.0
					: this.projSpendInfo[rowIndex].cost /
					  this.projSpendInfo[rowIndex].spendLimit;
		}
		return retValue;
	}

	public getProjectedSpendPercentStyle(code: string): any {
		let styles = {
			width: (this.getProjectedSpendPercent(code) * 100.0).toString() + "%"
		};
		return styles;
	}

	public getActualSpendValue(code: string): number {
		let retValue = 0.0;
		let si: SpendCeilingInfo = new SpendCeilingInfo();
		let rowIndex = _.findIndex(this.actSpendInfo, function(o) {
			return o.category === code;
		});
		if (rowIndex !== -1) {
			retValue = this.actSpendInfo[rowIndex].cost;
		}
		return retValue;
	}

	public getActualSpendPercent(code: string): number {
		let retValue = 0.0;
		let rowIndex = _.findIndex(this.actSpendInfo, function(o) {
			return o.category === code;
		});
		if (rowIndex !== -1) {
			retValue =
				this.actSpendInfo[rowIndex].spendLimit === 0.0
					? 0.0
					: this.actSpendInfo[rowIndex].cost /
					  this.actSpendInfo[rowIndex].spendLimit;
		}
		return retValue;
	}

	public getActualSpendPercentStyle(code: string): any {
		let styles = {
			width: (this.getActualSpendPercent(code) * 100.0).toString() + "%"
		};
		return styles;
	}

	public loadSpendCeilingInfo(dsc: DashboardSearchCriteria) {
		let handler = this;
		// Total Active TDL Numbers, Cost and Hours
		let loadprojspend = this.dashboardService.getDashboardProjectedSpendInfo(
			dsc
		);
		let loadactspend = this.dashboardService.getDashboardActualSpendInfo(dsc);

		this.busyLoadSpendCeilingInfo = forkJoin([
			loadprojspend,
			loadactspend
		]).subscribe(
			data => {
				if (data) {
					handler.projSpendInfo = data[0];
					handler.actSpendInfo = data[1];
				}
			},
			error => {
				this.showErrorFlag = true;
				// this.alerts = [];
				// this.alerts.push({type: 'warning', msg: error});
			}
		);
	}

	public refreshDashboardForDateRange(start, end) {
		let dsc: DashboardSearchCriteria = new DashboardSearchCriteria();
		dsc.dtFrom = start;
		dsc.dtTo = end;
		dsc.popCode = this.selpopCode;
		dsc.resourceID = -1;

		this.loadTDLInfo(dsc);
		this.loadTDLStatusInfo(dsc);
		this.loadTDLTypeInfo(dsc);
		this.loadSpendCeilingInfo(dsc);
	}

	public getProjectPercentComplete(proj: ProjectShortSummary) {
		let percent = 0.0;
		if (proj.planCost > 0.0) {
			percent = proj.actualCost / proj.planCost;
		}
		return percent;
	}
}
