import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import { FormsModule, ReactiveFormsModule} from "@angular/forms"
import { ChartsModule } from 'ng2-charts';
import { RouterModule } from "@angular/router";
import { DashboardComponent } from "./dashboard.component";
import { WidgetPage } from "./widgets.component";
import { MyDashboardPage } from "./mydashboard.component";
import { BarChartDemoComponent } from "./bar-chart-demo.component";

export const routes = [
	{ path: "", component: DashboardComponent, pathMatch: "full" }
];

@NgModule({
	imports: [CommonModule, FormsModule, ReactiveFormsModule, ChartsModule, RouterModule.forChild(routes)],
	declarations: [
		DashboardComponent,
		WidgetPage,
		MyDashboardPage,
		BarChartDemoComponent
	]
})
export class DashboardModule {
	static routes = routes;
}
