/*
 @author : Deloitte
 Service class to perform PermitPeriod related operations.
 */

import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { catchError, tap } from "rxjs/operators";
import { Credentials } from "../../login/model/credentials.model";
import { AuthService } from "../../login/services/auth.service";
import { DashboardSearchCriteria } from "../models/dash-search-criteria.model";
import { environment } from "./../../../environments/environment";

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable()
export class DashboardService {
	private dashboardurl = environment.apiurl + "/api/v1/dashboard";

	private credential: Credentials;

	constructor(private http: HttpClient, private authService: AuthService) {}

	public getTDLDashboardInfo(dashSearchCriteria: DashboardSearchCriteria) {
		let body = { dashSearchCriteria };
		return this.http
			.post(this.dashboardurl + "/tdl", dashSearchCriteria, httpOptions)
			.pipe(
				tap(_ => this.log("createProfile")),
				catchError(this.handleError<any>("createProfile"))
			);
	}

	public getDashboardProjectedSpendInfo(
		dashSearchCriteria: DashboardSearchCriteria
	) {
		return this.http
			.post(
				this.dashboardurl + "/projspend",
				dashSearchCriteria,
				httpOptions
			)
			.pipe(
				tap(_ => this.log("createProfile")),
				catchError(this.handleError<any>("createProfile"))
			);
	}

	public getTopUpcomingProjectsDue() {
		return this.http.get(this.dashboardurl + "/projtop5", httpOptions).pipe(
			tap(_ => this.log("createProfile")),
			catchError(this.handleError<any>("createProfile"))
		);
	}

	public getDashboardActualSpendInfo(
		dashSearchCriteria: DashboardSearchCriteria
	) {
		return this.http
			.post(this.dashboardurl + "/actspend", dashSearchCriteria, httpOptions)
			.pipe(
				tap(_ => this.log("createProfile")),
				catchError(this.handleError<any>("createProfile"))
			);
	}

	public getTDLDashboardTypeInfo(dashSearchCriteria: DashboardSearchCriteria) {
		return this.http
			.post(
				this.dashboardurl + "/project/type",
				dashSearchCriteria,
				httpOptions
			)
			.pipe(
				tap(_ => this.log("createProfile")),
				catchError(this.handleError<any>("createProfile"))
			);
	}

	public getTDLDashboardStatusInfo(
		dashSearchCriteria: DashboardSearchCriteria
	) {
		return this.http
			.post(
				this.dashboardurl + "/project/status",
				dashSearchCriteria,
				httpOptions
			)
			.pipe(
				tap(_ => this.log("createProfile")),
				catchError(this.handleError<any>("createProfile"))
			);
	}

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Admin message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Attribute Service: ${message}");
	}
}
